
.popup__heading {
  font-size: 24px;
  font-weight: 900;
  line-height: 29px;

  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
    
  padding: 0;
  margin: 0;
}

@media screen and (max-width: 480px) {
  .popup__heading {
    font-size: 20px;
    line-height: 25px;  
  }
}