
.sign__button {
  font-size: 18px;
  font-weight: 400px;
  line-height: 22px;
  text-align: center;

  width: 100%;
  height: 50px;

  background-color: #fff;
  color: #000;
  border: none;
  border-radius: 2px;
  outline: none;

  transition: opacity .3s linear;
  padding: 0;
  margin: 0;
  margin-top: auto;
}

.sign__button:hover {
  cursor: pointer;
  opacity: .85;
}
