
.loader {
  border: 5px solid #545454;
  border-top: 5px solid #fff;
  border-radius: 50%;

  width: 30px;
  height: 30px;
  animation: spin 2s linear infinite;

  padding: 0;
  margin: 0 auto;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}
